let lengthFreqMap = [|("1",0.027);("2",0.000257568344411811);("3",4.36149342814671E-05);("4",1.810604712137E-05);("5",1.00844498681708E-05);("6",6.742179232986E-06);("7",6.08192839360935E-06);("8",5.17368184211515E-06);("9",4.33606687724915E-06);("10",3.92415387705948E-06);("11",3.14628279863165E-06);("12",3.01500014386427E-06);("13",2.52392652134176E-06);("14",2.01737241657513E-06);("15",1.16226485206672E-06);("16",9.30988328446582E-07);("17",8.65358988660001E-07);("18",8.52860002809193E-07);("19",7.0619983686944E-07);("20",4.51455153639651E-07);("21",6.89125878395867E-07);("22",4.02343829642286E-07);("23",4.88336568600216E-07);("25",2.11559490631364E-07)|] |> Array.map( fun (n,p)->int(n),p) |> Map.ofArray

let wordRegex = System.Text.RegularExpressions.Regex("\w")

///Return the frequency of word if it is in top 5000 most frequent of Wikipedia. Otherwise uses term length to get a probability (counts of all terms of that length/total count/#terms of that length) again using Wikipedia. RETURNS 1.0 FOR NONWORDS
let Get( word : string ) =
    if wordRegex.IsMatch(word) then
        let query = word.Trim().ToLower()
        match wordFreqMap.TryFind( query ) with
        | Some( x ) -> x
        | None -> 
            match lengthFreqMap.TryFind( query.Length ) with
            | Some(y) -> y
            | None -> lengthFreqMap.[25] //the rarest event we know
    //Returns 1.0 for non words. This makes sense for how we use it but is otherwise wacky
    else
        1.0
